import React, { useEffect, useRef } from 'react';
import { NavLink } from 'react-router-dom';
import anime from 'animejs'; // https://animejs.com/documentation

// import Particles from 'react-particles-js';
// import { particlesOptions } from './exportedVariables/HomeParticlesOptions';

export default function Home() {

    const firstname = useRef();
    const lastname = useRef();
    const logobox = useRef();
    const logobox2 = useRef();
    const textdev = useRef();
    const textfront = useRef();
    const icon1 = useRef();
    const icon2 = useRef();
    const icon3 = useRef();
    const icon4 = useRef();

    useEffect(() => {

            anime({
                targets: firstname.current,
                translateX: "52vw",
                duration: 2550,
                delay: 140,
                easing: "easeInOutBack"
            })

            anime({
                targets: lastname.current,
                translateX: "52vw",
                duration: 2550,
                delay: 0,
                easing: "easeInOutBack"
            })

            anime({
                targets: logobox.current,
                opacity: [0, 1],
                translateY: [0, "0.8vmax"],
                duration: 1250,
                delay: 3000,
                easing: "easeOutSine"
            })

            anime({
                targets: logobox2.current,
                opacity: [0, 1],
                translateX: [0, "0.8vmax"],
                duration: 900,
                delay: 3400,
                easing: "easeOutSine"
            })

            anime({
                targets: textdev.current,
                opacity: [0, 1],
                translateX: [0, "2.8vmax"],
                duration: 1250,
                delay: 4250,
                easing: "easeOutSine"
            })

            anime({
                targets: textfront.current,
                opacity: [0, 1],
                translateX: [0, "-2.8vmax"],
                duration: 1250,
                delay: 4250,
                easing: "easeOutSine"
            })

            anime({
                targets: icon1.current,
                translateY: "86.3vh",
                duration: 3000,
                delay: 0,
                easing: 'easeInOutBack'
            })

            anime({
                targets: icon2.current,
                translateY: "86.3vh",
                duration: 3000,
                delay: 250,
                easing: 'easeInOutBack'
            })

            anime({
                targets: icon3.current,
                translateY: "86.3vh",
                duration: 3000,
                delay: 500,
                easing: 'easeInOutBack'
            })
            
            anime({
                targets: icon4.current,
                translateY: "86.3vh",
                duration: 3000,
                delay: 750,
                easing: 'easeInOutBack'
            })
    })

    return (
        <div className="home">

            <div className="background-svg">
            </div>

            <div ref={logobox} className="logo-box">
                <p ref={textdev}>Developer</p>
            </div>
            
            <div ref={logobox2} className="logo-box2">
                <p ref={textfront}>Frontend</p>
                <i className="code-icon material-icons">code</i>
            </div>

            <div className="box1">
            </div>
                
            <div className="box2">
            </div>
                
            <div className="box3"> 
            </div>
                
            <div className="box4">
            </div>

            {/* <Particles className="particles-js" params={particlesOptions} /> */}

            {/* <div className="particles-js-transparent-cover"></div> */}

            <div className="names">

                <div ref={firstname} className="first-name">
                    <p>EDDY</p>
                </div>

                <div ref={lastname} className="last-name">
                    <p>HAMZICH.</p>
                </div>

            </div>

            <NavLink to="/skills" className="call-to-action">
                <p> About me &nbsp;<i className="fas fa-angle-down"></i> </p>
            </NavLink>

            <div className="icons">
                <i ref={icon1} className="html fab fa-html5"></i>
                <i ref={icon2} className="css fab fa-css3-alt"></i>
                <i ref={icon3} className="javascript fab fa-js-square"></i>
                <i ref={icon4} className="react fab fa-react"></i>
            </div>

        </div>
    )
}