import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';

// Redux
import { createStore, compose, applyMiddleware } from 'redux';
import { Provider } from 'react-redux';
import { __rootReducer } from './components/redux/__rootReducer';
// Redux Persist
import storage from 'redux-persist/lib/storage';
import { persistStore } from 'redux-persist';
import { PersistGate } from 'redux-persist/integration/react';
import persistReducer from 'redux-persist/es/persistReducer';
// Redux Thunk
import thunk from 'redux-thunk';

const persistConfig = {
    key: "root",
    storage,
    whitelist: [""] // decide which reducer's state should be persisted after refresh ("" are necessary)!
}

// this is where we combine our rootReducer with persistedReducer!
const persistedReducer = persistReducer(persistConfig, __rootReducer)

// this is where we setup Redux DevTools!
const composeEnhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const store = createStore(
    persistedReducer,
    composeEnhancer(applyMiddleware(thunk))
)

const persistor = persistStore(store)

ReactDOM.render(
    <Provider store={store}>
        <PersistGate loading={null} persistor={persistor}>
            <App />
        </PersistGate>
    </Provider>,
    document.getElementById('root')
);