/*----- SCREEN SECTION REDUCER + ACTIONS BELOW -----*/

export const screenSectionReducer = (state = true, action) => { // there are only 2 screen sections thats why boolean value works!
    switch(action.type) {
        case "SWITCH_SCREEN":
            return !state;
        case "RESET_SCREEN":
            return state = true;
        default:
            return state;
    }
}

export const SWITCH_SCREEN = () => { return {type: "SWITCH_SCREEN"} }
export const RESET_SCREEN = () => { return {type: "RESET_SCREEN"} }
