/*----- DATA REDUCER + ACTIONS BELOW -----*/

export const dataReducer = (state = {dataLoading: true}, action) => {
    switch(action.type) {
        case "START_FETCH_DATA":
            return {...state }
        case "ERROR_FETCH_DATA":
            return {...state}
        case "FINISH_FETCH_DATA":
            return {...state, dataLoading: false, data: action.payload}
        default:
            return state;
    }
}

export const START_FETCH_DATA = () => {
    return (dispatch) => {
        Promise.all(
            [
            // Stocks api below
            fetch(`https://www.alphavantage.co/query?function=GLOBAL_QUOTE&symbol=TSLA&apikey=7MOENB7T9XZ2OF4F`).then(data => data.json()),
            fetch(`https://www.alphavantage.co/query?function=GLOBAL_QUOTE&symbol=AMZN&apikey=7MOENB7T9XZ2OF4F`).then(data => data.json()),
            fetch(`https://www.alphavantage.co/query?function=GLOBAL_QUOTE&symbol=AAPL&apikey=7MOENB7T9XZ2OF4F`).then(data => data.json()),
            fetch(`https://www.alphavantage.co/query?function=GLOBAL_QUOTE&symbol=AMD&apikey=7MOENB7T9XZ2OF4F`).then(data => data.json()),
            // Weather api below
            fetch(`https://api.openweathermap.org/data/2.5/weather?q=Tokyo&mode=json&units=metric&APPID=15a066a5e37a3b1fa863209d0ce1126a`).then(data => data.json()),
            fetch(`https://api.openweathermap.org/data/2.5/weather?q=Vienna&mode=json&units=metric&APPID=15a066a5e37a3b1fa863209d0ce1126a`).then(data => data.json()),
            fetch(`https://api.openweathermap.org/data/2.5/weather?q=Zurich&mode=json&units=metric&APPID=15a066a5e37a3b1fa863209d0ce1126a`).then(data => data.json()),
            fetch(`https://api.openweathermap.org/data/2.5/weather?q=Ljubljana&mode=json&units=metric&APPID=15a066a5e37a3b1fa863209d0ce1126a`).then(data => data.json())
            ]
        )
        .then(([TSLA, AMZN, AAPL, AMD, W1, W2, W3, W4]) => {
            [TSLA, AMZN, AAPL, AMD, W1, W2, W3, W4].some(dataArray => "Note" in dataArray) ? 
            dispatch({ type: "ERROR_FETCH_DATA" }) : 
            dispatch({ type: "FINISH_FETCH_DATA", payload: {TSLA, AMZN, AAPL, AMD, W1, W2, W3, W4} });
        })
    }
}